@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:200,300,400,700);
@import url(https://fonts.googleapis.com/css?family=Oswald:400,700);
@import url(https://fonts.googleapis.com/css?family=Yantramanav:100,300,400);
@import url(https://fonts.googleapis.com/css?family=Inconsolata:400,700);
a {
  text-decoration: none;
}

body {
  background: #141e30; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #243b55, #141e30); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#243b55),
    to(#141e30)
  );
  background: -webkit-linear-gradient(
    left,
    #243b55,
    #141e30
  );
  background: linear-gradient(
    to right,
    #243b55,
    #141e30
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  -webkit-font-smoothing: antialiased !important;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
  -ms-overflow-style: none;
}

@media (max-width: 577px){
  .mediaQuery{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto !important;
  }
}

.line{
  height: 5px;
  width: 10px;
  background-color: rgba(255, 255, 255, 0.774);
  border-radius: 5px;
  display: inline-block;
  -webkit-transition: .4s cubic-bezier(0.94,-0.60, 0, 1.92);
  transition: .4s cubic-bezier(0.94,-0.60, 0, 1.92);

}


.is-active span{
  background-color: white; 
  box-shadow:  1px 1px 30px  rgba(255, 255, 255, .5);
  width: 30px;
  -webkit-transition: .4s cubic-bezier(0.94,-0.60, 0, 1.92);
  transition: .4s cubic-bezier(0.94,-0.60, 0, 1.92);

}







 /* - - - - MAIN VIEW - - - - */

.fill:hover,
.fill:focus {
  box-shadow: inset 0 -3.25em 0 0 var(--hover);
}

.fill { 
  --hover: white;
}

button {
  -webkit-transition: .3s cubic-bezier(0.8, 0.5, 0.5, 1.4);
  transition: .3s cubic-bezier(0.8, 0.5, 0.5, 1.4);
}

button:hover, button:focus {
  border-color: var(--hover);
  color: #141E30;
}

.rotate {
  font-size: 1.2em;
}



 /* - - - - ALT VIEW - - - - */

.links{
  display: flex;
  justify-content: center;
  -webkit-animation: bigIn 0.3s cubic-bezier(0.8, 0.5, 0.5, 1.4) forwards;
          animation: bigIn 0.3s cubic-bezier(0.8, 0.5, 0.5, 1.4) forwards;
}

@media (min-width: 1536px){
  .links{
    font-size: 1.2em;
  }
}

@media (min-width: 1919px){
  .links{
    font-size: 1.5em;
  }
}

@media (max-width: 414px) { 
  .links{
    font-size: 1.6em;
    margin: 0;
  }
}

.links a {
  -webkit-transition: all .3s cubic-bezier(0.8, 0.5, 0.5, 1.4);
  transition: all .3s cubic-bezier(0.8, 0.5, 0.5, 1.4);
  color: white;
  margin: 1em;
  font-size: 1em;
  font-family: 'Yantramanav', sans-serif;
  font-weight: 300;
  background: -webkit-gradient(linear, left top, right top, color-stop(100%, white), to(white));
  background: -webkit-linear-gradient(left, white 100%, white 100%);
  background: linear-gradient(to right, white 100%, white 100%);
  background-size: 0px 2px;
  background-repeat: no-repeat;
  background-position: left 50%;
}

.links a:hover {
  background-size: 100% 2px;
}

@media (max-width: 414px) { 
  .links a{
    margin: .5em;
  }
}

@media (max-width: 320px) { 
  .links a{
    margin: .4em;
  }
}


@-webkit-keyframes bigIn {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bigIn {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.strike-through{
  text-decoration: line-through;
  
}



